import { useState } from "react"
import {
    Table,
    TableHeader,
    TableBody,
    TableColumn,
    TableRow,
    TableCell
  } from "@nextui-org/table";
  import {
    Modal, 
    ModalContent, 
    ModalHeader, 
    ModalBody, 
    ModalFooter,
    useDisclosure
  } from "@nextui-org/modal";

  import {Tooltip} from "@nextui-org/tooltip";
  import Image from 'next/image'
  import Link from 'next/link'
  import Edit from '@/public/images/edit.svg'
  import View from '@/public/images/view.png'
  import Trash from '@/public/images/lixeira.png'
  import { ModalDelete } from "../modalDelete/page";
  import { ModalEdit } from "../modalEdit/ModalEdit";
    import { EditStudent } from "./edit";

  type StudentType = {
    id: string,
    name: string,
    cpf: string,
    email: string,
    course: string,
  }


export function BoxStudent( {  }) {

    const studens: StudentType[] = [
        {
            id: '1',
            name: "Thiago Araujo",
            cpf: "16953214724",
            email: "thiagobarbosa.araujo@outlook.com",
            course: "automação"
        },

        {   
            id: '2',
            name: "Niely Alves",
            cpf: "1506325874",
            email: "niely@outlook.com",
            course: "instalação elétrica"
        },

        {
            id: '3',
            name: "Marilene Barbosa",
            cpf: "76525846893",
            email: "marilene@outlook.com",
            course: "comandos elétricos",
        },
    ]

    const [students, setStudents] = useState<string>('')
    const [studentId, setStudentId] = useState<string>('')

    const {isOpen, onOpen, onOpenChange} = useDisclosure();
    const [ modal, setModal ] = useState('')

    function deleteStudent(id: string) {
        console.log(id)
    }

    function EditStudentForm(id: string) {
        console.log("Editou", id)
    }

    return (
        <>
            <Table className="flex-1" removeWrapper>
            <TableHeader className="h-10">
                <TableColumn>NOME</TableColumn>
                <TableColumn>CPF</TableColumn>
                <TableColumn>EMAIL</TableColumn>
                <TableColumn>COURSE</TableColumn>
                <TableColumn>Actions</TableColumn>
            </TableHeader>
        
            <TableBody>
                {studens.map((student, index) => (
                    <TableRow className="shadow-md h-16" key={index}>
                        <TableCell>{student.name}</TableCell>
                        <TableCell>{student.cpf}</TableCell>
                        <TableCell>{student.email}</TableCell>
                        <TableCell>{student.course}</TableCell>
                        <TableCell>
                        <div className="flex gap-5">
                            
                            <Tooltip color="primary" content="Editar">
                                <Image onClick={() => {
                                    setStudentId(student.id)
                                    setModal('Edit')
                                    onOpen() 
                                    }} className="cursor-pointer"  width={24} height={24} src={Edit} alt='imagem de visualização' />
                            </Tooltip>

                            <Tooltip color="danger" content="Excluir">
                                
                                <Image onClick={() => {
                                    setStudentId(student.id)
                                    setModal('Delete')
                                    onOpen() 
                                    }} className="cursor-pointer"  width={24} height={24} src={Trash} alt='imagem de exclusão' />
                                
                            </Tooltip>
                        </div>
                        </TableCell>
                    </TableRow>
                ))}
            </TableBody>
            </Table>
            
            <Modal backdrop="opaque" isOpen={isOpen} isDismissable={false} onOpenChange={onOpenChange}>
                { 
                    modal === 'Delete' ? <ModalDelete title="Excluir Aluno ?" description="Tem certeza que deseja excluir o aluno ?" onSubmit={() => deleteStudent(studentId)} /> : <ModalEdit title="Edita Aluno" children={<EditStudent />} onSubmit={() => EditStudentForm(studentId)} />
                }
            </Modal>
        </>
    )
}