import { Button } from "@nextui-org/button";
import {
    Modal, 
    ModalContent, 
    ModalHeader, 
    ModalBody, 
    ModalFooter,
    useDisclosure
    } from "@nextui-org/modal";

    interface modalDeleteProps {
        title: string,
        children?: any
        description?: string
        onSubmit: () => void
     }


export function ModalEdit({ onSubmit, title, children }: modalDeleteProps) {

    return (
        <ModalContent>
        {(onClose) => (
            <>
            <ModalHeader className="flex flex-col gap-1">Editar Aluno</ModalHeader>
            <ModalBody>
                <p> 
                    Tem certeza que deseja editar esse aluno ?
                </p>

                {children}
                
            </ModalBody>
            <ModalFooter>
               
                <Button color="primary" onPress={onClose}>
                    Fechar
                </Button>

                <Button color="danger" variant="light" onClick={onSubmit}  onPress={onClose}>
                    Excluir
                </Button>
            </ModalFooter>
            </>
        )}
        </ModalContent>
    

    )
}